import { Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";

export default function Header1() {

  const navigate = useNavigate();
  const [isLogged, setIsLogged] = useState(false);

  useEffect(() => {
    const token = sessionStorage.getItem("token");
    setIsLogged(!!token);
  }, []);

  const handleLogout = () => {
    sessionStorage.clear();
    setIsLogged(false);
    navigate("/login");
  };

  return (

    <nav className="navbar navbar-expand-lg navbar-light bg-white shadow-sm sticky-top">

      <div className="container">

        {/* Logo */}
        <Link className="navbar-brand d-flex align-items-center" to="/">
          <i className="fa fa-book text-info me-2 fs-4"></i>
          <span className="fw-bold text-info fs-4">Engg.HUB</span>
        </Link>


        {/* Toggle */}
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
        >
          <span className="navbar-toggler-icon"></span>
        </button>


        {/* Menu */}
        <div className="collapse navbar-collapse" id="navbarNav">

          <ul className="navbar-nav ms-auto align-items-lg-center">

            <li className="nav-item">
              <Link className="nav-link px-3 fw-semibold" to="/">
                HOME
              </Link>
            </li>

            <li className="nav-item">
              <Link className="nav-link px-3 fw-semibold" to="/About1">
                ABOUT
              </Link>
            </li>

            <li className="nav-item">
              <Link className="nav-link px-3 fw-semibold" to="/Courses">
                COURSES
              </Link>
            </li>


            {/* Login Logout */}
            <li className="nav-item ms-lg-3 mt-2 mt-lg-0">

              {!isLogged ? (

                <Link
                  to="/login"
                  className="btn btn-primary py-4 px-lg-5 text-2xl"
                >
                  Login
                </Link>

              ) : (

                <button
                  onClick={handleLogout}
                  className="btn btn-danger px-4 py-2 login-btn"
                >
                  Logout
                </button>

              )}

            </li>

          </ul>

        </div>

      </div>

    </nav>

  );
}