import { Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import { ClipLoader } from "react-spinners";
import { ToastContainer, toast } from "react-toastify";
import apiServices from "../services/apiServices";
import "react-toastify/dist/ReactToastify.css";

export default function Login() {

  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const override = {
    display: "block",
    margin: "2rem auto",
  };

  // show message from sessionStorage once
  useEffect(() => {
    const msg = sessionStorage.getItem("message");

    if (msg) {
      toast.error(msg);
      sessionStorage.removeItem("message");
    }
  }, []);

  const handleForm = async (e) => {

    e.preventDefault();

    if (!email || !password) {
      toast.error("Please enter email and password");
      return;
    }

    try {

      setLoading(true);

      const res = await apiServices.login({
        email,
        password,
      });

      setLoading(false);

      if (res.data.success) {

        toast.success(res.data.message);

        const user = res.data.data;

        sessionStorage.setItem("user_name", user.name);
        sessionStorage.setItem("user_email", user.email);
        sessionStorage.setItem("token", res.data.token);
        sessionStorage.setItem("user_type", user.userType);
        sessionStorage.setItem("authenticate", true);
        sessionStorage.setItem("user_id", user._id);
        sessionStorage.setItem("status", user.status);

        // navigation based on userType
        if (user.userType == 1) {
          navigate("/admin");
        }
        else if (user.userType == 3) {
          sessionStorage.setItem("teacher_id", user.teacherId);
          navigate("/teacher");
        }
        else {
          sessionStorage.setItem("student_id", user.studentId);
          navigate("/student");
        }

      } else {

        // user not found or wrong password
        toast.error(res.data.message || "User not found");

      }

    } catch (error) {

      setLoading(false);

      if (error.response) {

        toast.error(
          error.response.data.message ||
          "User not found or invalid credentials"
        );

      } else if (error.request) {

        toast.error("Server not responding");

      } else {

        toast.error("Something went wrong");

      }

      console.log(error);

    }

  };

  return (
    <>
      <ToastContainer position="top-right" autoClose={2000} />

      {/* Header */}
      <div className="container-fluid bg-primary py-5 mb-4">
        <div className="container text-center">
          <h1 className="text-white">Login</h1>

          <nav>
            <Link to="/" className="text-white">
              Home
            </Link>
            <span className="text-white"> / Login</span>
          </nav>
        </div>
      </div>

      {/* Loader */}
      {loading && (
        <ClipLoader loading={loading} cssOverride={override} size={60} />
      )}

      {/* Login Form */}
      <div className="container">

        <div className="row justify-content-center">

          <div className="col-lg-6 col-md-8 col-sm-12">

            <form
              onSubmit={handleForm}
              className="shadow p-4 mb-5 bg-white rounded"
            >

              <h3 className="text-center mb-4">Login</h3>

              {/* Email */}
              <div className="form-floating mb-3">
                <input
                  type="email"
                  className="form-control"
                  placeholder="Email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
                <label>Email Address</label>
              </div>

              {/* Password */}
              <div className="form-floating mb-3">
                <input
                  type="password"
                  className="form-control"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
                <label>Password</label>
              </div>

              {/* Button */}
              <button
                className="btn btn-primary w-100 py-2"
                disabled={loading}
              >
                {loading ? "Logging in..." : "Login"}
              </button>

            </form>

          </div>

        </div>

      </div>
    </>
  );
}